<template>
  <div id="container">

    <div id="take-part">
      <div class="tp-bg"></div>
      <div class="tp-title" v-if="user.lang === 'ru'">Принять участие</div>
      <div class="tp-title" v-else>Ishtrok etish</div>
      <div class="tp-pic">
        <img src="../files/participation.svg" alt="Take Part">
      </div>
      <div class="tp-desc" v-if="user.lang === 'ru'">В своём стремлении<br>улучшить пользовательский..</div>
      <div class="tp-desc" v-else>O'z harakatingizbilan<br>Imkoniyatni oshiring</div>
      <div class="tp-form">
        <div class="btn tp-ticket-rm" id="removeTicket" @click.prevent="ticketCount < 3 ? 2 : ticketCount--">
          <ion-icon name="remove"></ion-icon>
        </div>
        <div class="tp-ticket-count" id="countTicket">2</div>
        <div class="btn tp-ticket-add" id="addTicket" @click.prevent="ticketCount++">
          <ion-icon name="add"></ion-icon>
        </div>
      </div>
      <div class="ticket-buy-btn" @click="buyTicket" v-if="user.lang === 'ru'">Участвовать</div>
      <div class="ticket-buy-btn" @click="buyTicket" v-else>Qatnashish</div>
    </div>

    <BottomNavigationMenu active="participation" />
  </div>
</template>

<script>
import HeaderUserBar from "@/components/HeaderUserBar";
import BottomNavigationMenu from "@/components/BottomNavigationMenu";
import axios from "axios";

export default {
  name: "Participation",
  data: () => ({
    user_id: 0,
    user: null,
    ticketCount: 2,
  }),
  components: {
    HeaderUserBar,
    BottomNavigationMenu
  },
  async mounted() {
    document.title = 'Принять участие';
    const telegram = window.Telegram.WebApp;
    const userData = telegram.initDataUnsafe.user;

    // this.user_id = 5296876609
    this.user_id = userData.id

    await axios.get('https://server.gold-ticket.uz/api/user/' + this.user_id)
        .then(response => {
          this.user = response.data.data
        })
        .catch(error => console.log(error));
  },
  methods: {
    buyTicket()  {
      window.Telegram.WebApp.sendData({buy: this.ticketCount})
    },
    async createTicket() {
      const telegram = window.Telegram.WebApp;
      await axios.post('https://server.gold-ticket.uz/api/ticket/' + this.user_id, {
        tickets_count: this.ticketCount
      })
      .then(response => {
        if (this.user.lang === 'ru') {
          if (response.data.status === "success" && this.ticketCount !== 0) {
            telegram.showPopup({
              'title': 'Участие',
              'message': `Вам успешно начислили ${this.ticketCount} тикетов`,
              'buttons': [
                {
                  'id': 'success',
                  'type':'ok',
                  'text': "Продолжить"
                }
              ]
            })
          } else {
            telegram.showPopup({
              'title': 'Участие',
              'message': `Произлашло ошибка, повторите позже`,
              'buttons': [
                {
                  'id': 'error',
                  'type':'ok',
                  'text': "Повторить"
                }
              ]
            })
          }
        } else {
          if (response.data.status === "success" && this.ticketCount !== 0) {
            telegram.showPopup({
              'title': 'Qatnashish',
              'message': `Sizga ${this.ticketCount} ticketlar taqdim etildi\n\nBotga /pay komandasini berib ticketlar uchun to'lovni amalga oshiring`,
              'buttons': [
                {
                  'id': 'success',
                  'type':'ok',
                  'text': "Davom etish"
                }
              ]
            })
          } else {
            telegram.showPopup({
              'title': 'Qatnashish',
              'message': `xatolik yuz berdi iltimos keynroq qayta urunib ko'ring`,
              'buttons': [
                {
                  'id': 'error',
                  'type':'ok',
                  'text': "Qayta takrorlash"
                }
              ]
            })
          }
        }
        this.ticketCount = 0
      }).catch(() => {
        this.ticketCount = 0
      })
    }
  },
  watch: {
    ticketCount() {
      document.getElementById('countTicket').innerText = this.ticketCount
    }
  }
}
</script>

<style scoped>

@media screen and (max-height: 748px) {
  #container {
    padding: 0 25px;
    margin-bottom: 110px !important;
  }
}

@media screen and (max-height: 652px) {
  #container {
    padding: 0 25px;
    margin-bottom: 210px !important;
  }
}
</style>
